import { Box } from "@mui/material";
import React from "react";
import "./css/loading.css";

const Loading = () => {
    return (
        <Box
            display={"flex"}
            alignItems={"center"}
            justifyContent={"center"}
            width={"100%"}
            height={"100vh"}
            bgcolor={"black"}
            flexDirection={"column"}
        >
            <Box>
                <img
                    src="/assets/images/mortal-kombat-logo.png"
                    alt=""
                    style={{
                        filter: "invert(39%) sepia(99%) saturate(684%) hue-rotate(156deg) brightness(104%) contrast(101%)",
                        animation: "loading 2s",
                        animationIterationCount: "infinite",
                    }}
                    height={"200rem"}
                />
            </Box>
            <Box fontSize={"4rem"}>
                <span>Loading</span>
            </Box>
            <Box fontSize={"1.5rem"} marginTop={"-1.3rem"}>
                <span>Please be patient</span>
            </Box>
        </Box>
    );
};

export default Loading;
